<template>
	<view style="display: flex; flex: 1; height: 100%; flex-flow: column nowrap;background-color:#F8F8F8;">


		<scroll-view scroll-y :style="{height: style.contentViewHeight +'px;'}" @click="tap_table">


			<block v-for="(item,index) in hot_list_comment" :key="index">
				<view class="" style="flex-flow: row nowrap;  padding-top: 10upx;position: relative;" v-if="item.comment_id==comment_id">
					<image class="imageview" :src="item.picture ? item.picture :loacal_avatarUrl_loc"></image>
					<view class="comment_list_view1">
						<view class="" style="width: 600upx; font-size: 25upx; color: #007AFF; padding-top:10upx ;">{{item.nick_name}}</view>
						<view class="" style="width: 600upx; font-size: 25upx; color: black; padding-top:10upx ;">{{item.comment}}</view>
						<view class="" style="flex-flow: row nowrap; padding-top:10upx ; padding-bottom: 10upx; ">
							<view class="" style="width:500upx; font-size: 25upx; color:#555555;">{{item.time}}</view>
							<view style="width: 100upx;justify-content: flex-end;">
								<view class="icontext" style=" font-size: 30upx; color:#DC143C " @click="click_dianzhan(index)">{{item.can_dianzhan!=false ? '&#xe610;':'&#xe60c;'}}</view>
								<view class="" style=" font-size: 25upx; color:#555555;" @click="click_dianzhan(index)">{{item.dianzhan!=0 ? item.dianzhan:'点赞'}}</view>
							</view>
						</view>
					</view>
				</view>
			</block>
			<view class="" style="width: 750upx;height: 10upx;background-color: #EFEFEF;">

			</view>
			<block v-for="(item,index) in hot_list_comment" :key="index">
				<view class="" style="flex-flow: row nowrap;  padding-top: 10upx;position: relative;" v-if="item.par_comment_id==comment_id&&item.type=='zipinglun'"
				 v-show="!item.comment_isdislike">
					<image class="imageview" :src="item.picture ? item.picture :loacal_avatarUrl_loc"></image>
					<view class="icontext shanchuview" @click="tap_dislike(index)">&#xe65c;</view>
					<view class="comment_list_view">
						<view class="" style="width: 600upx; font-size: 25upx; color: #007AFF; padding-top:10upx ;">{{item.nick_name}}
							<image class="biaoqing_image_view" :src="'../../static/z'+item.biaoqing_num+'.png'"></image>
						</view>
						<view class="" style="width: 600upx; font-size: 25upx; color: black; padding-top:10upx ;">{{item.comment}}</view>
						<view class="" style="flex-flow: row nowrap; padding-top:10upx ; padding-bottom: 10upx; ">
							<view class="" style="width:500upx; font-size: 25upx; color:#555555;">{{item.time}}</view>
							<view style="width: 100upx;justify-content: flex-end;">
								<view class="icontext" style=" font-size: 30upx; color:#DC143C " @click="click_dianzhan(index)">{{item.can_dianzhan!=false ? '&#xe610;':'&#xe60c;'}}</view>
								<view class="" style=" font-size: 25upx; color:#555555;" @click="click_dianzhan(index)">{{item.dianzhan!=0 ? item.dianzhan:'点赞'}}</view>
							</view>
						</view>
					</view>
				</view>

			</block>
		</scroll-view>


		<chat-input @send-message="getInputMessage" @biaoQingSelect="biaoqing_select" :ziduanmodifyflag="ziduan_modify_flag"
		 :taptablefuc="taptablefuc"></chat-input>

	</view>
</template>

<script>
	import chatInput from '../../components/im-chat/chatinput.vue';
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				style: {
					pageHeight: 0,
					contentViewHeight: 0,
					footViewHeight: 90,
					mitemHeight: 0,
				},
				win_bottom: 0,
				select_view_flag: false,
				select_biaoqing_flag: false,
				ziduan_modify_flag: false,
				biaoqing_num: 0,

				hot_list_comment: [],
				luyouData: null,
				loacal_avatarUrl_loc: "",


				comment_message: "",
				card_id: "",
				comment_id: "",
				huifu: 0,
				persondata: {
					comment_candianzhan: [],
					comment_isdislike: [],
					handle_isdislike: [],
				},
			}
		},
		onLoad() {
			this.loacal_avatarUrl_loc = this.loacal_avatarUrl
		},
		computed: {
			...mapState(['hasLogin', 'userInfo', 'phonenum', 'openid'])

		},
		components: {
			chatInput,
		},
		onLoad: function(opotion) {


			this.card_id = opotion.card_id
			this.comment_id = opotion.comment_id
			this.huifu = Number(opotion.huifu) + 1;
			console.log("card_id:", opotion.card_id)
			console.log("comment_id:", opotion.comment_id)
			console.log("huifu:", opotion.huifu)
			this.getData_hot_list_comment();
			// this.style.pageHeight = uni.getSystemInfoSync().windowHeight;

			this.style.contentViewHeight = uni.getSystemInfoSync().windowHeight - 80 * (uni.getSystemInfoSync().windowWidth /
				750); //像素
			// console.log(this.style.pageHeight, this.style.contentViewHeight)

		},

		methods: {
			...mapMutations(['login', 'logout']),

			getInputMessage: function(message) { //获取子组件的输入数据
				// console.log(message);
				// this.addMessage('customer', message.content, false);
				this.win_bottom = 0;
				console.log(message)
				this.upload_to_server({
					'form': 'comment',
					'comment_message': message.content,
					'card_id': this.card_id,
					'nickName': this.userInfo.nickName,
					'phonenum': this.phonenum,
					'openid': this.openid,
					'avatarUrl': this.userInfo.avatarUrl,
					'par_comment_id': this.comment_id,
					'comment_type': "zipinglun",
					'huifu': this.huifu,
					'biaoqing_num': this.biaoqing_num,
				});



				this.huifu = Number(this.huifu) + 1;
				console.log('huifu', this.huifu)
				setTimeout(() => this.getData_hot_list_comment(), 1000)
				// this.getData_hot_list_comment();
			},


			tap_table() {
				// this.select_biaoqing_flag = false;
				// this.select_view_flag = false;
				// this. win_bottom=0;
				this.taptablefuc = !this.taptablefuc;

			},



			biaoqing_select(biaoqing_num) {
				this.biaoqing_num = biaoqing_num;
				console.log(this.biaoqing_num)
			},



			tap_dislike(index) {
				var that = this;
				this.hot_list_comment[JSON.stringify(index)].fandui = Number(this.hot_list_comment[JSON.stringify(index)].fandui) +
					1
				this.hot_list_comment[JSON.stringify(index)].comment_isdislike = true;
				this.persondata['comment_isdislike'].push(this.hot_list_comment[JSON.stringify(index)].comment_id);
				uni.setStorage({
					key: 'persondata',
					data: that.persondata,
					success: function() {
						console.log('setdata_persondata_success');
					}
				});
				this.upload_to_server({
					'card_id': this.card_id,
					'form': 'comment_fandui',
					'dianzhan_num': this.hot_list_comment[JSON.stringify(index)].dianzhan,
					'comment_id': Number(this.hot_list_comment[JSON.stringify(index)].comment_id),
					'fandui': this.hot_list_comment[JSON.stringify(index)].fandui,
					'nickName': this.userInfo.nickName,
					'phonenum': this.phonenum,
					'openid': this.openid,
					'avatarUrl': this.userInfo.avatarUrl,
				});



				// this.hot_list_comment[JSON.stringify(index)].type = "null";

			},

			click_dianzhan(index) {
				var that = this;
				if (this.hot_list_comment[JSON.stringify(index)].can_dianzhan != false) {
					console.log(JSON.stringify(index));
					this.hot_list_comment[JSON.stringify(index)].dianzhan = Number(this.hot_list_comment[JSON.stringify(index)].dianzhan) +
						1;
					this.hot_list_comment[JSON.stringify(index)].can_dianzhan = false
					this.persondata['comment_candianzhan'].push(this.hot_list_comment[JSON.stringify(index)].comment_id);
					uni.setStorage({
						key: 'persondata',
						data: that.persondata,
						success: function() {
							console.log('setdata_persondata_success');
						}
					});
					this.upload_to_server({
						'card_id': this.card_id,
						'form': 'comment_dianzhan',
						'dianzhan_num': this.hot_list_comment[JSON.stringify(index)].dianzhan,
						'comment_id': Number(this.hot_list_comment[JSON.stringify(index)].comment_id),
						'fandui': this.hot_list_comment[JSON.stringify(index)].fandui,
						'nickName': this.userInfo.nickName,
						'phonenum': this.phonenum,
						'openid': this.openid,
						'avatarUrl': this.userInfo.avatarUrl,
					});

				} else {
					uni.showToast({
						title: "谢谢!已经投票过啦",
						icon: "none"
					})
				}
			},

			getData_hot_list_comment: function() {
				var that = this;
				uni.request({
					url: 'https://kaikaiomg.applinzi.com/uni_app_download.php',
					method: "POST",
					data: {
						'form': 'hot_card_list_comment',
						'card_id': that.card_id,
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (ret) => {
						if (ret.statusCode !== 200) {
							console.log("请求失败:", ret)
						} else {
							console.log("hot_card_list_comment:", ret.data)
							that.hot_list_comment = ret.data.data;
							uni.getStorage({
								key: 'persondata',
								success: function(res) {
									console.log("find_persondata", res.data);
									that.persondata = res.data;
									for (let i = 0; i < that.hot_list_comment.length; i++) {
										if (that.persondata.comment_candianzhan.indexOf(that.hot_list_comment[i].comment_id) != -1) {
											that.hot_list_comment[i].can_dianzhan = false
										}
										if (that.persondata.comment_isdislike.indexOf(that.hot_list_comment[i].comment_id) != -1) {
											that.hot_list_comment[i].comment_isdislike = true
										}
									}

								},
								fail: function() {
									console.log("havent_find_persondata");
								}
							});
							// console.log("hot_card_list_comment:", ret.data.data)
						}
					}
				});
			},




		}
	}
</script>

<style>
	.comment_style {
		display: flex;
		position: fixed;
		/* height: 90upx; */
		min-height: 60px;
		flex-flow: column nowrap;
		width: 750upx;
		left: 0upx;
		overflow: hidden;
		border-top: #555555 solid 1upx;
		background-color: #EFEFEF;
		bottom: 0upx;
		align-content: center;

	}

	.shanchuview {
		position: absolute;
		right: 45upx;
		top: 13upx;
		font-size: 26upx;
	}

	.huifuview {
		font-size: 20upx;
		position: relative;
		top: 4upx;
		left: 10upx;
		/* 	border-radius: 30upx;
		background-color: #8F8F94;
		height: 22upx;
		line-height: 22upx;
		padding: 0 5upx; */
	}

	.imageview {
		width: 40upx;
		height: 44upx;
		border-radius: 44px;
		margin-left: 40upx;
		margin-right: 30upx;
		margin-top: 23upx;
	}


	.grey {
		color: grey;
	}

	.black {
		color: black;
	}

	.white {
		color: white;
	}

	.bac_red {
		background-color: red;
	}

	.bac_purple {
		background-color: purple;
	}

	.bac_yellow {
		background-color: yellow;
	}

	.bac_blue {
		background-color: blue;
	}

	.bac_pink {
		background-color: pink;
	}

	.bac_green {
		background-color: SpringGreen;
	}

	.bac_brown {
		background-color: Sienna;
	}




	.select_view_item {
		width: 100%upx;
		height: 60upx;
		padding: 10upx;
		font-size: 30upx;
		justify-content: center;
		align-content: center;
	}



	.comment_list_view {
		width: 600upx;
		flex-flow: column;
		border-bottom: #EFEFEF solid 1upx;
	}

	.comment_list_view1 {
		width: 600upx;
		flex-flow: column;
	}

	.biaoqing_image_view {

		margin: 8upx 0upx 0upx 13upx;
		height: 20upx;
		width: 20upx;


	}
</style>



